import React from "react";
import { Link } from "gatsby";
import styles from "./styles.module.css";
import ListIcon from "./icon-list.svg";
import GridIcon from "./icon-grid.svg";
import MoreIcon from "./icon-more.svg";
import LessIcon from "./icon-less.svg";
import GroupsIcon from "./icon-groups.svg";
import { navigate } from '@reach/router';
import withLocation from "../Common/withLocation";

const isGrid = path => path === "/" || path === "/all";
const isList = path => path.includes("list");
const isGroups = path => path.includes("function") || path.includes("visualization-technique") || path.includes("information-type") || path.includes("axis-direction");

const HeadlinePanel = ({location:{pathname}, search:{sortBy, sortOrder}, includeVariations}) => (
  <>
    <div className={styles.headline}>
      <h1 className={styles.title}>Charts and Diagrams</h1>
      <p className={styles.description}>
        for infographics and data visualization
      </p>
    </div>
    <div className={styles.controls}>
      <MoreButton includeVariations={includeVariations} />
      <ChartViewSwitch pathname={pathname} includeVariations={includeVariations} />
      <SortButton pathname={pathname} sortBy={sortBy} sortOrder={sortOrder}/>
    </div>
  </>
);

function SortButton({ pathname, sortBy, sortOrder }) {
  return (
    <div className={`${styles.sortBtnGroup} ${isGroups(pathname) && styles.sortBtnHidden}`}>
        <span className={styles.sortBy}>
          {sortBy === "name"
            ? <ParamLink text="name" params={`?sortBy=rel&sortOrder=${sortOrder ? sortOrder : "desc"}`}
                         path={pathname} title="sort by relevance" />
            : <ParamLink text="relevance"
                         params={`?sortBy=name&sortOrder=${sortOrder ? sortOrder : "desc"}`}
                         path={pathname} title="sort order" />
          }
        </span>
        <span className={styles.sortOrder}>
          {sortOrder === "asc"
              ? <ParamLink text="▲" params={`?sortBy=${sortBy ? sortBy : "rel"}&sortOrder=desc`}
                         path={pathname} title="sort in descending order" />
            : <ParamLink text="▼" params={`?sortBy=${sortBy ? sortBy : "rel"}&sortOrder=asc`}
                         path={pathname} title="sort order" />
          }
        </span>
    </div>
  )
}

function MoreButton({ includeVariations }) {
  return (
    <Link to={includeVariations ? "../" : "all"} title={`show ${ includeVariations ? "less" : "more" } chart variations`}>
      <div className={styles.showMore}>
        {includeVariations
          ? <>less <LessIcon/></>
          : <>more <MoreIcon/></>
        }
      </div>
    </Link>
  )
}
function ChartViewSwitch({pathname, includeVariations}) {
  return (
    <span className={styles.gridListSwitch}>
      {isGrid(pathname)
        ? <span className={styles.gridBtn}><GridIcon /></span>
        : <Link to={includeVariations ? "/all" : "/"} title="chart grid">
            <span className={styles.gridBtn}><GridIcon /></span>
          </Link>
      }
      {isList(pathname)
        ? <span className={styles.listBtn}><ListIcon /></span>
        : <Link to={includeVariations ? "/list/all" : "/list"} title="chart list">
            <span className={styles.listBtn}><ListIcon /></span>
          </Link>
      }
      {isGroups(pathname)
        ? <span className={styles.groupsBtn}><GroupsIcon /></span>
        : <Link to={includeVariations ? "/function/all" : "/function"} title="chart functions">
            <span className={styles.groupsBtn}><GroupsIcon /></span>
          </Link>
      }

    </span>
  );
}

const ParamLink = ({text, path, params, title}) => (
  <a href={path + params} onClick={(e) => {e.preventDefault(); navigate(path + params)}} title={title}>
    <div>{text}</div>
  </a>
);

export default withLocation(HeadlinePanel);
