import React from 'react'
import {Location} from '@reach/router'
import queryString from 'querystring'

/*
 * Supplements component with access to query string parameter values.
 * from https://medium.com/@crisfitkin/how-to-get-query-string-parameter-values-in-gatsby-f714161104f
 */
const withLocation = ComponentToWrap => props => (
  <Location>
    {({ location, navigate}) => (
      <ComponentToWrap
        {...props}
        location={location}
        navigate={navigate}
        search={location.search ? queryString.parse(location.search.replace("?","")) : {}}
      />
    )}
  </Location>
)

export default withLocation
