import { withoutSlashes } from "../../utils";
import React from "react";
import styles from "./styles.module.css";


export default function ChartIcon({ slug, isVariation, showStack = false }) {
  const spritesFile = isVariation ? "variationIconSprites" : "iconSprites";
  return (
    <span className={showStack ? `${styles.icon} ${styles.stack}`: styles.icon}>
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0, 0, 256, 256">
        <use xlinkHref={`/${spritesFile}.svg#` + withoutSlashes(slug)}/>
      </svg>
    </span>
  )
}
